.app {
  height: 100vh;
  width: 100vw;
  display: flex;
  background-color: #020230;
  color: white;
  overflow: hidden;

}

.main {
  width: 75%;
  background-image: url("./assests/bg.jpg");
  background-size: cover;
  display: flex;
  flex-direction: column;
}

.top {
  height: 50%;
  position: relative;
}


.timer {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 5px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-weight: 700;
  position: absolute;
  bottom: 10px;
  left: 80px;
}

.bottom {
  height: 50%;
}

.trivia {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.questions {
  width: 80%;
  background: linear-gradient(#100241, black);
  border: 2px solid white;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  font-size: 20px;
}

.answers {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.answer {
  width: 40%;
  padding: 10px;
  text-align: center;
  background: linear-gradient(#0e0124, #22074d);
  border-radius: 15px;
  border: 1px solid white;
  font-size: 20px;
  font-weight: 300;
  cursor: pointer;
  margin: 0 10px 20px 10px;
}

.answer:hover,
.answer.active {
  background: mediumblue;
}

.answer.correct {
  animation: correct 3s ease forwards;
}

@keyframes correct {

  0%,
  22%,
  42% {
    background: mediumblue;
  }

  20%,
  40%,
  60% {
    background: linear-gradient(#0e0124, #22074d);
  }

  62%,
  100% {
    background: green;
  }
}

.answer.wrong {
  animation: wrong 3s ease forwards;
}

@keyframes wrong {

  0%,
  22%,
  42% {
    background: mediumblue;
  }

  20%,
  40%,
  60% {
    background: linear-gradient(#0e0124, #22074d);
  }

  62%,
  100% {
    background: crimson;
  }
}

.pyramid {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.moneylist {
  list-style: none;
  width: 100%;
  padding: 20px;
}

.moneylistitem {
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 10px;
}

.moneylistitem.active {
  background-color: teal;
}

.moneylistitemnumber {

  font-size: 18px;
  font-weight: 100;
  width: 30%;
}

.moneylistitemamout {
  font-size: 20px;
  font-weight: 300;
}

.earnings {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.start {
  width: 250px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  right: 0;
  margin: auto;
  justify-content: space-around;
}

.startInput {
  width: 100%;
  height: 30px;
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
}

.startInput:focus {
  outline: none;
}

.startButton {
  width: 100%;
  height: 30px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}

@media screen and (max-width:55em)  {
  .app {
    height:100vh;
    width:100vw;
    display: flex;
    background-color: #020230;
    color: white;
    overflow:hidden
  
  }
  .moneylistitemnumber {
    font-size: 14px;
    font-weight: 80;
    width: 5%;
  }
  .moneylistitemamout {
    font-size: 13px;
    font-weight: 200;
    margin-left: 30px;
  }
  .moneylistitem{
    padding-bottom: 1rem;
  }
  .timer {
    margin-bottom: 4rem;
  }
  .answers {
    margin:2rem;
  }
}
